import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { UserDict } from "models/user/user.dict";
import moment from "moment";
import { NgxFiteloConfig, ngxFiteloConfig } from "../../tokens";

@Injectable({
  providedIn: 'root',
})
export class OauthResource {

  constructor(
    @Inject(ngxFiteloConfig) private config: NgxFiteloConfig,
    private httpClient: HttpClient,
  ) {}

  async registerUser(credentials) {
    return this.httpClient.post(`${this.config.API_URL}/oauth/register`, credentials).toPromise()
  }

  async getUser(uid: string): Promise<UserDict> {
    const ud = await this.httpClient.get<UserDict>(`${this.config.API_URL}/users/${uid}`).toPromise()
    return ud;
  }

  async updateUser(uid: string, userDict: UserDict): Promise<UserDict> {
    const ud = await this.httpClient.post<UserDict>(`${this.config.API_URL}/oauth/updateuser/${uid}`, userDict).toPromise();
    return ud;
  }

  async updateSubscription(uid, plan, razorpay_payment_id) {
    const update = {
      subscription: {
        planName: plan.name,
        discount: plan.discount,
        startdate: moment().format('YYYYMMDD'),
        price: plan.price,
        trasactionId: razorpay_payment_id
      },
      subscription_duration: plan.validity,
      // TODO: fix it
      subscription_endDate: moment().add(plan.validity, 'months').format('YYYYMMDD'),
      subscription_status: 'Existing',
    }

    const ud = this.httpClient.put(`${this.config.API_URL}/users/${uid}`,update).toPromise()
    return ud;
  }


}
